___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: #a5e3e7
}


* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.5em;
  line-height: 1.5em;
}

.container {
  max-width: 840px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

form button {
  border: none;
  background: #e43434;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
  display: block;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 10px;
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
  transition: 0.3s all;
}

form button:hover{
  background: rgb(177, 44, 44)
}

button:active {
    transform: scale(0.98);
    /* Scaling button to 0.98 to its original size */
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    /* Lowering the shadow */
}

h1 {
  text-align: center;
}

label{
  display: inline-block;
  font-size: 23px;
}

form input {
  width: 20px;
  height: 20px;
}

.copy, #copy, .reload {
  width: 50px;
  height: 50px;
}

.generate {
  animation: is-rotating 0.6s
}

.char {
  width: 60px;
  height: 30px;
  font-size: 28px;
  margin-bottom: 30px;
}

.result {
  font-size: 1.55em;
  text-align: center;
  color: blue;
  display: inline-block;
}

.join, .img, .lineChar {
  display: flex;
  justify-content: space-between;
}

.lineChar {
  width: 350px;
}

.slidecontainer {
  width: 100%; 
}


.slider {
  -webkit-appearance: none;  
  appearance: none;
  width: 100%; 
  height: 9px; 
  background: #d3d3d3; 
  outline: none; 
  opacity: 0.7; 
  -webkit-transition: .2s; 
  transition: opacity .2s;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #e51a1a;
  cursor: pointer;
  border-radius: 50%;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #e43434;
  cursor: pointer;
}

.power {
  background-color: rgb(245, 245, 245);
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  width: 100%;
  height: 11px;
  margin-bottom: 30px;
}
.power0 {
  background-color: rgb(245, 245, 245);
  width: 0%;
}
.power1 {
  background-color: rgb(223, 102, 97);
  width: 25%;
}
.power2 {
  background-color: rgb(239, 194, 15);
  width: 50%;
}
.power3 {
  background-color: rgb(0, 168, 120);
  width: 75%;
}
.power4 {
  background-color: rgb(0, 107, 77);
  width: 100%;
}

.power0, .power1, .power2, .power3, .power4 {
  height: 10px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  transition: 0.6s;
}

@keyframes is-rotating {
  to {
    transform: rotate(1turn);
  }
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAEA;EACE;AACF;;;AAGA;EACE,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,SAAS;EACT,UAAU;EACV,gCAAgC;EAChC,oCAAoC;EACpC,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,gBAAgB;EAChB,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,mBAAmB;EACnB,WAAW;EACX,eAAe;EACf,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,WAAW;EACX,mBAAmB;EACnB,mBAAmB;EACnB,gDAAgD;EAChD,oBAAoB;AACtB;;AAEA;EACE;AACF;;AAEA;IACI,sBAAsB;IACtB,gDAAgD;IAChD,gDAAgD;IAChD,wBAAwB;AAC5B;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE;AACF;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,kBAAkB;EAClB,WAAW;EACX,qBAAqB;AACvB;;AAEA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,WAAW;AACb;;;AAGA;EACE,wBAAwB;EACxB,gBAAgB;EAChB,WAAW;EACX,WAAW;EACX,mBAAmB;EACnB,aAAa;EACb,YAAY;EACZ,uBAAuB;EACvB,uBAAuB;EACvB,+BAA+B;EAC/B,4BAA4B;EAC5B,8BAA8B;EAC9B,2BAA2B;AAC7B;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,wBAAwB;EACxB,gBAAgB;EAChB,WAAW;EACX,YAAY;EACZ,mBAAmB;EACnB,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,oCAAoC;EACpC,+BAA+B;EAC/B,4BAA4B;EAC5B,8BAA8B;EAC9B,2BAA2B;EAC3B,WAAW;EACX,YAAY;EACZ,mBAAmB;AACrB;AACA;EACE,oCAAoC;EACpC,SAAS;AACX;AACA;EACE,mCAAmC;EACnC,UAAU;AACZ;AACA;EACE,mCAAmC;EACnC,UAAU;AACZ;AACA;EACE,kCAAkC;EAClC,UAAU;AACZ;AACA;EACE,iCAAiC;EACjC,WAAW;AACb;;AAEA;EACE,YAAY;EACZ,+BAA+B;EAC/B,4BAA4B;EAC5B,8BAA8B;EAC9B,2BAA2B;EAC3B,gBAAgB;AAClB;;AAEA;EACE;IACE,wBAAwB;EAC1B;AACF","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');\r\n\r\n:root {\r\n  --primary-color: #a5e3e7\r\n}\r\n\r\n\r\n* {\r\n  box-sizing: border-box;\r\n  outline: 0;\r\n}\r\n\r\nbody {\r\n  margin: 0;\r\n  padding: 0;\r\n  background: var(--primary-color);\r\n  font-family: 'Open sans', sans-serif;\r\n  font-size: 1.5em;\r\n  line